import React, {useEffect} from 'react';
import style from './Stats.module.scss'
import {poolChecker} from "../../../Filters";
import Charts from "../../Charts/Charts";
import Fetcher from "../../Fetcher/Fetcher";

const Stats = ({
                   fee,
                   luck,
                   hashrate,
                   unit,
                   height,
                   lastBlockFound,
                   minPayment,
                   thisPool,
                   miners,
                   type,
                   charts,
               }) => {

    return (
        <div className={style.stats}>
            {charts && charts.length !== 0 ?
                <div className={style.graph}>
                    <div className={style.chartsGraph}>
                        <Charts charts={charts} text={`  ${thisPool} `}/>
                    </div>
                </div> : <div className={style.graph}><Fetcher/></div>}
                <div className={style.items}>
                    <div className={style.item}> : {isNaN(luck) ? 'n/a' : `${luck.toFixed(0)} %`}</div>
                    <div className={style.item}> : {fee} %</div>
                    <div className={style.item}> : {hashrate}{unit}</div>
                    <div className={style.item}> : {height}</div>
                    <div className={style.item}> : {lastBlockFound}</div>
                    <div className={style.item}> : {minPayment} {poolChecker(thisPool)}</div>
                    <div className={style.item}>: {miners}</div>
                    <div className={style.item}> : {type}</div>
                </div>
        </div>
    );
};

export default Stats;